import React, { useState } from "react";

export default function AddComment({ articleName, setArticleInfo }) {
	let [userName, setUserName] = useState("");
	let [comment, setComment] = useState("");

	const addComment = async () => {
		const result = await fetch(`/api/articles/${articleName}/add-comments`, {
			method: "post",
			body: JSON.stringify({ username: userName, text: comment }),
			headers: {
				"Content-Type": "application/json",
			},
		});
		const body = await result.json();
		setArticleInfo(body);
		setUserName = "";
		setComment = "";
	};

	return (
		<section className="add-comment-section" style={{ marginTop: "1.2rem" }}>
			<hr />
			<label style={{ display: "block" }}>
				Name:
				<input
					style={{ marginLeft: "1rem", marginBottom: "0.5rem" }}
					type="text"
					value={userName}
					onChange={(e) => setUserName(e.target.value)}
				></input>
			</label>
			<label>
				Add Comment:
				<textarea
					rows="3"
					cols="50"
					value={comment}
					onChange={(e) => setComment(e.target.value)}
					style={{ marginTop: "0.5rem" }}
				/>
			</label>
			<button onClick={() => addComment()}>Add Comment</button>
			<hr />
		</section>
	);
}
