import React from "react";

export default function CommentsList({ comments }) {
	return (
		<>
			{comments.length === 0 ? (
				<h3>No comments found 🙂</h3>
			) : (
				<>
					<h3>Comments: </h3>
					{comments.map((comment, key) => (
						<section className="comments-section" key={key}>
							<h4>{comment.username}</h4>
							<p>{comment.text}</p>
						</section>
					))}
				</>
			)}
		</>
	);
}
