import React from "react";

export default function UpvotesSection({
	upvotes,
	articleName,
	setArticleInfo,
}) {
	const upvoteAction = async () => {
		const result = await fetch(`/api/articles/${articleName}/upvote`, {
			method: "post",
		});
		const body = await result.json();
		setArticleInfo(body);
	};

	return (
		<section className="upvotes-section">
			<button onClick={() => upvoteAction()}> Upvote</button>

			<p style={{ marginLeft: "20px" }}>This article has {upvotes} upvotes.</p>
		</section>
	);
}
